<script>
	import { final_score, final_outgoing } from './store.js';
  import { onMount } from "svelte";
  import { fly, fade } from "svelte/transition";
  import { tweened } from 'svelte/motion';
	import { cubicOut } from 'svelte/easing';
  import {qnaList} from "./data";
  import {replace} from 'svelte-spa-router';

  let current_idx = 0;
  let outgoing_score = 0;
  let english_score = 0;

  onMount(() => {
    
  })

  const progress = tweened(0, {
		duration: 400,
		easing: cubicOut
	});

  $: question = qnaList[current_idx]

  function goNext(selected){
    
    if(selected.score){
      english_score += selected.score
    }else if(selected.outgoing){
      outgoing_score += selected.outgoing
    }

    progress.set((current_idx + 1) / qnaList.length)

    if(current_idx+1 < qnaList.length){
      current_idx++
    }else{
      goResult()
    }
    


  }

  function goResult(){
    final_score.set(english_score)
    final_outgoing.set(outgoing_score)
    replace('/result/')
  }

</script>

<body>
  <div id="wrap">
   <section id="qna" class="container" >
      <div class="status-bar">
        {#key current_idx}
          <div class="status" style="width:{((current_idx + 1)/qnaList.length)*100}%" ></div>
        {/key}
      </div>
      <!-- <progress class="status-bar" value="{$progress}" style="color:blanchedalmond">ds</progress> -->
      {#key current_idx}
      <div class="q box" in:fade={{duration: 400, delay: 600}}  out:fade={{delay: 400}} >{@html question.q}</div>
        <div class="answer">
          {#each question.a as a}
            <button in:fly={{ y: +20, delay: 600}} out:fly={{x : +20}} class="a box" on:click="{() => goNext(a)}">{a.answer}</button>
          {/each}
        </div>
      {/key}
    </section>
</body>

<style>
  /* qna로 넘어갈 때 display: block; */
.status-bar {
  margin: 0 12.5% 20px;
  width: 75%;
  height: 15px;
  border: 3px solid #ddd;
  border-radius: 20px;
  background: #fff;
  animation: fade-in 0.3s ease-in-out;
}
.status {
  height: 100%;
  border-radius: 20px;
  background-color: rgba(255, 85, 0, 0.7);
}
.box {
  margin: 0 auto;
  padding: 20px;
  width: 80%;
  box-shadow: #ddd 0px 0px 10px 5px;
  border-radius: 20px;
  background: rgb(252, 252, 252);
}
.q {
  box-sizing: border-box;
  margin-bottom: 30px;
  font-size: 27.2px;
  font-size: 1.2rem;
  animation: fade-in 0.5s ease-in-out 0.3s forwards;
}
.answer {
  width: 100%;
}
.a {
  margin: 20px 10% 0 10%;
  padding: 15px;
  border: none;
  font-size: 17.6px;
  font-size: 1.1rem;
  font-weight: bolder;
  text-align: left;
  transition: background-color, color 0.2s linear;
  cursor: pointer;
}
.a:hover, .a:focus {
  background: #222;
  color: #eee;
}
.a:last-child {
  margin-bottom: 60px;
}

@media all and (min-width: 768px) {
  .status-bar {
    margin: 0px 15% 30px;
    width: 70%;
    height: 20px;
  }
  .box {
    margin: 0 auto;
    padding: 30px;
    width: 80%;
    box-shadow: #ddd 0px 0px 10px 5px;
    border-radius: 20px;
    background: rgb(252, 252, 252);
  }
  .q {
    margin: 0 auto;
    margin-bottom: 50px;
    font-size: 32px;
    font-size: 1.8rem;
  }
  .answer {
    margin: 0 10%;
    width: 80%;
    font-size: 24px;
    font-size: 1.5rem;
  }
  .a {
    margin: 15px 0;
    padding: 20px 30px;
    width: 100%;
    font-size: 20.8px;
    font-size: 1.3rem;
  }
}

@media all and (min-width: 1024px) {
  .status-bar {
    margin: 0px 15% 40px 15%;
  }
  .q {
    margin-bottom: 80px;
    font-size: 40px;
    font-size: 2rem;
  }
  .answer {
    font-size: 28.8px;
    font-size: 1.8rem;
  }
  .a {
    padding: 25px 35px;
    width: 100%;
    font-size: 22.4px;
    font-size: 1.4rem;
  }
}

@media all and (min-width: 1440px) {
  .status-bar {
    margin: 0 5% 40px 5%;
    width: 90%;
  }
  .q {
    margin-bottom: 80px;
    width: 100%;
    font-size: 40px;
    font-size: 2rem;
  }
  .answer {
    margin: 0;
    width: 100%;
    font-size: 28.8px;
    font-size: 1.8rem;
  }
}

</style>