<script>
	import { final_score, final_outgoing } from './store.js';
  import {infoList} from './data';
  import { onMount } from 'svelte';
  import { fly, fade } from "svelte/transition";

  import { tweened } from 'svelte/motion';
  import { quintOut } from 'svelte/easing';
  import CopyClipBoard from "./components/CopyClipboard.svelte"

  let score =  $final_score
  let outgoing = $final_outgoing
  let level = ''
  let type = ''
  let loading = true;

  let book = {}

  function copy(){
      const app = new CopyClipBoard({
        target: document.getElementById('clipboard'),
        props: { 'value' : 'https://aerinlee.github.io/bookclass-test/' },
      });
      alert("복사되었습니다.")
      app.$destroy();
    }

  const progress = tweened(0, {
    duration: 4000,
    easing: quintOut
  });

  onMount(() => {

    setTimeout(()=>{
      progress.set(100)
    }, 1500)

    setTimeout(()=>{
      calc()
      loading = false
    }, 4400)
   
  })

  function calc(){
    if(score > 17){
    level = '고급반'
  }else if(score > 10){
    level = '중급반'
  }else{
    level = '초급반'
  }
  
  if(level == '중급반'){
    if(outgoing > 9){
      type = 'high'
    }else if(outgoing < 9){
      type = 'low'
    }else{
      type = 'middle'
    }
  }else{
    if(outgoing >= 9){
      type = 'high'
    }else{
      type = 'low'
    }
  }
  for(let i=0; i < infoList.length; i++){
    if(infoList[i].outgoing == type && infoList[i].level == level){
      book = infoList[i]
    }
  }
}

function goLearning(){
 window.open(book.url)
}

  

</script>

{#if loading}
  {#key loading}
  <section id="calc" out:fade>
    <div class="calc-bar">
      CALCULATING
      <div class="calc" style="width:{$progress}%"></div>
    </div>
    {#key loading}
    <p class="wait" in:fly={{y: -20, duration: 1000}}>잠시 기다려주세요...</p>
    {/key}
  </section>
  {/key}
{:else}
{#key loading}
  <section id="result" class="container" in:fade={{delay:500}}>
        <div id="score-box">
          <div class="p title">당신에게 딱 맞는 북클래스는...</div>
        </div>
        <div id="desc-box">
          <div class="art"><img src="img/{book.img}" alt="{book.name}" title="{book.name}"></div>
          <div class="result title">{@html book.name}</div>
          <div class="res desc">{book.desc}</div>
        </div>
        <hr>
        <p>공유하기</p>
        <div class="hash">#MissionEnglish #북클래스 #영어회화</div>
        <button on:click="{()=>copy()}" class="btn">링크 복사하기</button>
        <hr>
        <div class="caution">
          <p>
            Youtube 명강사가 만든 브랜드 섹션 북클래스!<br>
            이젠 유튜브로 듣지만 말고 직접 말해보아요.<br>
            370개의 상황대화는 보너스!!<br><br>
            지금 바로 신청하세요~ (BBS 참고)
            <br><br>
            <button on:click="{()=>goLearning()}" class="btn">수강신청 하러가기</button>
          </p>
  <!--         <button onclick="() => window.open(infoList[grade].url)" class="btn">신청하러 가기</button> -->
        </div>

        <hr>

  </section>
  <div id="clipboard"></div>
  {/key}
{/if}
